/**
 * HandTracker Integrates MediaPipe Hands for hand tracking
 */
export class HandTracker {
  constructor() {
    this.hands = null;
    this.onResultsCallback = null;
  }

  /**
   * Initialize MediaPipe Hands
   */
  async initialize() {
    // Note: In Vite, we'll use the copied model files from public directory
    this.hands = new window.Hands({
      locateFile: (file) => {
        // Since files are copied to /mediapipe/hands/ by vite-plugin-static-copy
        return `/mediapipe/hands/${file}`;
      }
    });

    this.hands.setOptions({
      maxNumHands: 2,
      modelComplexity: 1,
      minDetectionConfidence: 0.7,
      minTrackingConfidence: 0.5
    });

    this.hands.onResults((results) => {
      if (this.onResultsCallback) {
        this.onResultsCallback(results);
      }
    });

    // Loading the JS library should be done in index.html via script tag 
    // or through npm package if using a more modern setup.
    // Here we assume the CDN/Local script is loaded.
  }

  /**
   * Start detection on a video frame
   */
  async send(videoElement) {
    if (this.hands) {
      await this.hands.send({ image: videoElement });
    }
  }

  /**
   * Register results callback
   */
  onResults(callback) {
    this.onResultsCallback = callback;
  }
}
